/**
 * WrapPageElement.
 * 
 * Implement the Gatsby's WrapPageElement Browser API.
 * 
 * @see: https://www.gatsbyjs.org/docs/browser-apis/
 *
 * @param	element:	The Page element (React component).
 * @param	props: 		The Page properties. Provide same data to Layout as Page element will get
 *           			  including location, data, etc - you don't need to pass it.
 * 
 * @return	Layout:		Layout React Component (JSX).
 */

/**
 * Dependencies.
 */
import * as React from 'react'
import Layout from '../components/layout'
import LocalesContextProvider from '../components/locales/LocalesContextProvider'

/**
 * Smooth scroll
 */
if (typeof window !== "undefined") {
  // eslint-disable-next-line global-require
  require("smooth-scroll")('a[href*="#"]', {

      // Speed
      speed: 100,

      // Easing
      easing: 'easeInOutQuint',
      /*
      easing: 'easeInOutCubic', // Easing pattern to use
      customEasing: function (time) {
  
          // Function. Custom easing pattern
          // If this is set to anything other than null, will override the easing option above
  
          // return <your formulate with time as a multiplier>
  
          // Example: easeInOut Quad
          return time < 0.5 ? 2 * time * time : -1 + (4 - 2 * time) * time;
  
      }
      */

  })
}

/**
 * Page Wrapper React component (JSX).
 */
export default ({ element, props }) => {

  const layoutProps = element.props.children[1].type.pageMeta || props
  const subLayoutProps = element.props.children[1].type.subLayout || {}

  return <LocalesContextProvider pageContext={props.pageContext}><Layout pageMeta={layoutProps} subLayout={subLayoutProps} {...props}>{element}</Layout></LocalesContextProvider>

}
